.worksheet-cta {
    // position: relative;

    &--section {
        padding-top: 105px;
        padding-bottom: 0;

        .container {
            position: relative;
            color: #fff;
            background: $gradient-bluepurple;
            border-radius: 14px;

            &::before,
            &::after {
                content: "";
                background-color: #fff;
                border-radius: 50%;
                position: absolute;
            }

            &::before {
                width: 59px;
                height: 59px;
                bottom: calc(100% + 87px);
                left: 13px;
            }

            &::after {
                width: 26px;
                height: 26px;
                bottom: calc(100% + 36px);
                left: 0;
            }
        }

        &.has-section-circlebg {
            &::after {
                top: -10%;
            }
        }
    }

    .shape {

        &-1,
        &-2,
        &-3 {
            position: absolute;

            img,
            svg {
                width: 100%;
                height: auto;
            }
        }

        &-1 {
            width: 146px;
            top: 36px;
            left: 39%;
        }

        &-2 {
            width: 26px;
            top: 4px;
            left: 70%;
        }

        &-3 {
            width: 35px;
            bottom: 0;
            left: 72%;
        }
    }

    .list-tick {
        margin-bottom: 0;
    }

    .text {
        width: 514px;
        padding-top: 67px;
        padding-bottom: 80px;
        margin-left: auto;

        h2 {
            color: #fff;
            margin-bottom: 10px;
        }
    }

    .media {
        position: absolute;
        bottom: 0;
        left: 0;
        padding-right: 74px;
        width: calc(100% - 514px);

        img {
            max-width: 667px;
            width: 100%;
        }
    }
}