.testimonials-home {
    position: relative;

    &--section {
        padding-top: 50px;

        .container {
            @media (min-width: 1367px) {
                padding: 0;
            }
        }
    }

    .inner {
        padding: 48px;
        background-color: #fff;
        text-align: center;
        border-radius: 14px;
    }

    .shapesbg {
        .shape-2 {
            top: -142px;
            right: 37px;
        }
    }

    h2 {
        margin-bottom: 43px;
    }

    .testimonials-box {
        max-width: 810px;
        @include margin-auto;

        .content {
            p {
                font-size: 26px;
                line-height: 44px;
                font-weight: 200;
                font-style: italic;
            }
        }

        .author {
            margin-top: 38px;

            p {
                color: $color-gray-1;
                font-size: 16px;
                font-weight: 600;
                margin-bottom: 0;
                line-height: 24px;
            }
        }

        .btn {
            font-family: $font-title;
            font-weight: 700;
            margin-top: 24px;
        }
    }
}