.skill-test {
    .section-card {}

    &--card {
        padding-left: 30px;
        padding-right: 30px;

        &-header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 32px;
        }

        &-body {
            .correct-answer {
                padding: 20px;

                img {
                    width: 100%;
                    max-width: 392px;
                }
            }
        }

        &-footer {
            margin-top: 45px;
        }

        &-overlay {
            z-index: 1;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            padding: 48px;
            background-color: rgba(#fff, .94);
            display: flex;
            flex-flow: column;
            align-items: center;
            justify-content: center;

            h1 {
                margin-bottom: 24px;
            }

            .correct-answer {
                display: flex;
                align-items: center;
                margin-bottom: 57px;

                p {
                    color: $color-primary;
                    font-family: $font-title;
                    font-size: 20px;
                    font-weight: 700;
                    margin-bottom: 0;
                    margin-right: 18px;
                }
            }
        }
    }

    &--question {
        margin-bottom: 60px;

        .title {
            text-align: center;

            h1 {
                margin-bottom: 6px;
            }

            p {
                color: $color-text-light;
            }
        }

        &-nums {
            text-align: center;

            h5 {
                font-family: $font-title;
                font-size: 86px;
                font-weight: 700;
                margin-bottom: 0;
            }
        }

        &-media {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            justify-content: center;
            margin-top: 58px;

            img {
                width: 100%;
                max-width: 140px;

                &:not(:last-child) {
                    margin-right: 54px;
                }
            }
        }
    }

    &--ans {
        &--row {
            display: flex;
        }

        &--col {
            &-options {
                flex: 0 0 calc(100% - 362px);
                padding-right: 30px;
            }

            &-ansbox {
                flex: 0 0 362px;
            }
        }

        &-options {
            h4 {
                font-family: $font;
                font-size: 19px;
                font-weight: 600;
                color: $color-text-2;
                opacity: .68;
            }

            .options {
                display: flex;
                flex-wrap: wrap;
                list-style-type: none;
                padding-left: 0;
                margin: 0 -20px;

                .option {}
            }

            &-theme-1 {
                .options {
                    .option {
                        padding: 20px;

                        &-inner {
                            width: 92px;
                            height: 92px;
                            background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='28' ry='28' stroke='%23333' stroke-width='2' stroke-dasharray='6%2c 14' stroke-dashoffset='10' stroke-linecap='square'/%3e%3c/svg%3e");
                            border-radius: 28px;
                            transform: rotate(45deg);
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }

                        .num-wrap {
                            position: relative;
                            color: $color-blue-1;
                            font-family: $font-museo;
                            font-weight: 900;
                            font-size: 34px;
                            width: 58px;
                            height: 58px;
                            border-radius: 50%;
                            display: inline-flex;
                            align-items: center;
                            justify-content: center;
                            background-color: $color-gray;
                            transform: rotate(-45deg);

                            &::before,
                            &::after {
                                content: "";
                                position: absolute;
                                border-radius: 50%;
                            }

                            &::before {
                                z-index: -1;
                                top: 5px;
                                left: 5px;
                                width: 9px;
                                height: 9px;
                                background-color: $color-blue-1;
                            }

                            &::after {
                                width: 18px;
                                height: 18px;
                                background-color: $color-yellow-light;
                                bottom: 0;
                                right: 0;
                            }
                        }
                    }
                }
            }

            &-theme-2 {
                .options {
                    margin-left: -17px;
                    margin-right: -17px;

                    .option {
                        padding-left: 17px;
                        padding-right: 17px;

                        &-inner {
                            z-index: 1;
                            position: relative;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: 70px;
                            height: 70px;

                            &::before,
                            &::after {
                                content: "";
                                z-index: -1;
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                background-position: 0;
                                background-repeat: no-repeat;
                                background-size: contain;
                            }

                            &::before {
                                background-image: url("data:image/svg+xml;base64,")
                            }

                            &::after {
                                background-image: url("data:image/svg+xml;base64,")
                            }
                        }

                        .num {
                            &-wrap {}

                            color: #fff;
                            font-family: $font-title;
                            font-size: 33px;
                            font-weight: 700;
                        }
                    }
                }
            }
        }

        &-ansbox {
            position: relative;
            // border: 1px dashed $color-border-1;
            background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='28' ry='28' stroke='%23333' stroke-width='2' stroke-dasharray='6%2c 14' stroke-dashoffset='10' stroke-linecap='square'/%3e%3c/svg%3e");
            border-radius: 28px;

            .ansbox-tag {
                position: absolute;
                top: 12px;
                right: 15px;
                font-size: 12px;
                font-weight: 600;
                color: $color-black-1;
                background-color: $color-yellow;
                padding: 6px 8px;
                border-radius: 4px;
                display: inline-flex;
            }

            &.is-empty {
                .ansbox-body {
                    position: relative;
                    width: 100%;
                    min-height: 160px;
                    padding: 12px 38px;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    p {
                        color: $color-black-1;
                        font-size: 19px;
                        font-weight: 600;
                        margin-bottom: 0;
                        opacity: .24;
                    }
                }
            }
        }
    }

    &--col-stat .section-card {
        padding: 38px;
    }

    &--stat {
        display: flex;
        flex-wrap: wrap;
        flex-flow: column;

        &-card {
            position: relative;
            text-align: center;
            padding: 28px 48px;
            border-radius: 8px;
            color: $color-green-1;
            background-color: $color-green-1-bg;

            &:not(:last-child) {
                margin-bottom: 11px;
            }

            .icon {
                position: absolute;
                top: 15px;
                left: 10px;
                width: 30px;

                img,
                svg {
                    width: auto;
                    height: auto;
                    max-width: 100%;
                }
            }

            h4 {
                font-family: $font;
                font-size: 12px;
                font-weight: 800;
                max-width: 75px;
                @include margin-auto;
                margin-bottom: 4px;
            }

            p {
                font-size: 16px;
                font-weight: 900;
                margin-bottom: 0;

                .num {
                    font-size: 22px;
                    font-weight: 700;
                    line-height: 1;

                    &.size-lg {
                        font-size: 34px;
                        font-weight: 900;
                    }
                }
            }

            &.theme-green {
                color: $color-green-1;
                background-color: $color-green-1-bg;
            }

            &.theme-violet {
                color: $color-violet-text;
                background-color: $color-violet-bg;
            }

            &.theme-blue {
                color: $color-blue-2;
                background-color: $color-blue-2-bg;
            }
        }
    }
}