.breadcrumb {
    list-style-type: none;
    padding-left: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;

    li {
        position: relative;
        font-size: 12px;
        font-weight: 600;
        color: $color-text;

        &:not(:last-child) {
            margin-right: 25px;

            &::after {
                content: "";
                position: absolute;
                top: 50%;
                left: calc(100% + 8px);
                transform: translateY(-50%);
                width: 5px;
                height: 8px;
                background-image: url("data:image/svg+xml;base64,");
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
            }
        }

        a {
            color: $color-primary;
        }
    }
}