.footer {
    position: relative;

    &-clouds {

        img {
            width: 100%;
            margin-bottom: -4px;
        }
    }

    &-character {
        position: absolute;
        top: -260px;
        left: 0;
        width: 168px;

        img {
            width: 100%;
        }
    }

    &-top,
    &-bot {
        background-color: #fff;
    }

    &-top {
        padding-bottom: 65px;

        .row {
            margin-left: -20px;
            margin-right: -20px;
        }

        [class^="col-"] {
            padding-left: 20px;
            padding-right: 20px;
        }

        .inner {
            position: relative;
        }

        .col-about {
            // border-right: 1px solid #D0D0D0;
            padding-right: 30px;

            &::after {
                content: "";
                position: absolute;
                top: 0;
                right: 15px;
                width: 1px;
                height: 100%;
                background-color: #D0D0D0;
            }

            .logo {
                max-width: 199px;
                margin-bottom: 24px;
            }

            p {
                font-size: 14px;
                line-height: 24px;
            }
        }

        h3 {
            font-family: $font;
            font-size: 14px;
            font-weight: 800;
            margin-bottom: 20px;
        }

        ul {
            list-style-type: none;
            padding-left: 0;
            margin: 0;

            li {
                font-size: 12px;

                &:not(:last-child) {
                    margin-bottom: 16px;
                }
            }
        }

        .social-group {
            margin-top: 35px;

            a {
                display: inline-flex;
                width: 40px;
                height: 40px;

                img {
                    width: 100%;
                }

                &:not(:last-child) {
                    margin-right: 11px;
                }
            }
        }
    }

    &-bot {

        .inner {
            padding: 24px 0 47px;
            border-top: 1px solid #D0D0D0;
        }

        .copyright {
            p {
                font-size: 12px;
                font-weight: 500;
                color: $color-gray-1;
                margin-bottom: 0;
            }
        }

        &-nav {
            list-style-type: none;
            padding-left: 0;
            margin: 0;
            display: flex;
            justify-content: flex-end;

            li {
                font-size: 12px;
                font-weight: 700;
                color: $color-gray-1;

                &:not(:last-child) {
                    margin-right: 8px;
                }
            }
        }
    }
}