.nav-tabs {
    display: flex;
    list-style-type: none;
    padding-left: 0;
    margin: 0 0 24px;

    @media (max-width: 991px) {
        display: none;
    }

    .nav-tabs-link {
        cursor: pointer;

        &:not(:last-child) {
            margin-right: 10px;
        }

        .grade {
            .text {
                color: $color-text-light;
            }

            .shape {
                svg path {
                    fill: $color-gray-2;
                }
            }
        }

        &:hover {
            .grade .shape svg path {
                fill: rgba($color-green-light, .5);
            }
        }

        &.active {
            .grade {
                .text {
                    color: #fff;
                }

                .shape svg path {
                    fill: $color-green-light;
                }
            }
        }
    }

    &-content {
        .nav-tabs {
            &-link {
                cursor: pointer;

                @media (min-width: 992px) {
                    display: none;
                }
            }

            &-pane {
                &:not(.active) {
                    display: none;
                }
            }
        }
    }
}