.list-yellow-tick {
    list-style-type: none;
    padding-left: 0;
    margin-top: 0;

    li {
        position: relative;
        padding-left: 78px;
        min-height: 63px;
        display: flex;
        align-items: center;
        font-size: 19px;
        font-weight: 700;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 66px;
            height: 66px;
            display: inline-flex;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            background-image: url("data:image/svg+xml;base64,");
        }
    }
}

.list-purple-tick {
    list-style-type: none;
    padding-left: 0;
    margin: 0;

    li {
        position: relative;
        padding-left: 36px;
        font-size: 14px;
        font-weight: 700;
        min-height: 28px;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 28px;
            height: 28px;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            background-image: url("data:image/svg+xml;base64,")
        }
    }
}