h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $font-title;
    margin-top: 0;
}

h1 {
    color: $color-primary;
    font-size: 48px;
    font-weight: 700;
}

h2 {
    color: $color-primary;
    font-size: 40px;
    font-weight: 700;
    line-height: 125%;

    &.size-lg {
        font-size: 60px;
        line-height: 64px;
    }
}

h3 {
    font-size: 22px;
    font-weight: 600;
    line-height: 124%;
    margin: 0 0 14px;
}

a {
    color: inherit;
    text-decoration: none;
    outline: none;
    transition: all 0.3s ease;

    &:hover {
        color: $color-primary;
        text-decoration: none;
        outline: none;
    }

    &:focus {
        outline: none;
    }
}


a,
button {
    transition: all .3s ease-out;
    cursor: pointer;
}

p {
    font-size: 18px;
    line-height: 166.667%;
    margin: 0 0 26px;
}