.whytrust {
    &--section {
        padding-top: 72px;
        padding-bottom: 110px;
    }

    .circlesbg {
        top: -450px;
    }

    >.row>.col-text {
        @media (min-width: 992px) {
            padding-right: 32px;
        }
    }

    .col-text {
        h2 {
            margin-bottom: 12px;
        }

        p {
            color: $color-black-2;
            font-size: 16px;
            font-weight: 700;
        }
    }

    &-cards {
        .row {
            margin-left: -9px;
            margin-right: -9px;
        }

        .col {
            padding-left: 9px;
            padding-right: 9px;
        }

        .card {
            padding: 40px 24px 42px;
            text-align: center;
            background-color: #fff;
            box-shadow: 0 0 24px rgba($color-black, .06);
            border-radius: 18px;

            .icon {
                width: 74px;
                @include margin-auto;
                margin-bottom: 24px;

                img {
                    max-width: 100%;
                }
            }

            h4 {
                font-family: $font;
                font-size: 16px;
                font-weight: 700;
                margin-bottom: 8px;
                text-transform: capitalize;
            }

            p {
                font-size: 12px;
                line-height: 18px;
                margin-bottom: 0;
            }
        }
    }
}