.test-audio {
    text-align: center;
    margin-bottom: 10px;

    &-btn {
        width: 42px;
        height: 42px;
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        background-color: $color-yellow;
        border: 0;

        svg,
        img {
            width: 20px;
            height: auto;
        }
    }
}