.inden-learning {
    &--section {
        padding-top: 30px;
        padding-bottom: 126px;
    }

    .inner {
        max-width: 834px;
        @include margin-auto;
    }

    h2 {
        margin-bottom: 49px;
    }

    ul {
        display: flex;
        text-align: left;

        li {
            width: 33.33333%;
            padding-left: 40px;
            padding-right: 12px;
            font-size: 16px;
            line-height: 24px;
            font-weight: 600;
        }
    }

    .btn {
        margin-top: 68px;
        font-family: $font-title;
        font-size: 19px;
        font-weight: 700;
    }
}