.circlesbg {
    z-index: -2;
    position: absolute;
    top: 0;
    left: calc(50% + 32px);
    transform: translateX(-50%);
    width: 1747px;
    max-width: 100vw;

    svg {
        width: 100%;
        height: auto;
    }
}

.shapesbg {
    .shape {

        &-1,
        &-2,
        &-3 {
            z-index: -1;
            position: absolute;

            svg,
            img {
                width: 100%;
                height: auto;
            }
        }

        &-1 {
            top: 143px;
            left: -129px;
            width: 360px;
        }

        &-2 {
            width: 503px;
            top: 0;
            right: -110px;
        }

        &-3 {
            top: 261px;
            right: -201px;
            width: 164px;
            height: 164px;
        }
    }
}