input:not([type="submit"]),
select,
textarea {
    font-family: $font;
    font-size: 14px;
    font-weight: 800;
    border: 1px solid $color-border;
    background-color: $color-gray-2;
    border-radius: 3px;

    &:focus {
        outline: none;
    }
}

input:not([type="submit"]),
select {
    height: 52px;
    padding: 5px 16px 5px 20px;
    width: 100%;

    &::placeholder {
        opacity: 1;
        color: $color-black-3;
    }
}

select {
    color: $color-black-3;

    &,
    option {
        font-family: $font;
    }
}