.share-social {
    display: flex;
    align-items: center;

    p {
        color: $color-text;
        font-size: 12px;
        font-weight: 600;
        margin-bottom: 0;
    }

    ul {
        display: flex;
        flex-wrap: wrap;
        list-style-type: none;
        padding-left: 10px;
        margin: 0;

        li {
            &:not(:last-child) {
                margin-right: 8px;
            }

            img,
            svg {
                width: 29px;
                height: auto;
            }
        }
    }
}