.skill-listing {
    &-item {
        .title {
            margin-bottom: 18px;

            *:last-child {
                margin-bottom: 0;
            }

            h2 {
                font-size: 34px;
                margin-bottom: 10px;
            }

            p {
                color: $color-text-1;
                font-size: 16px;
                font-weight: 600;
            }
        }

        h4 {
            font-family: $font;
            font-size: 18px;
            font-weight: 800;
            color: $color-text;
            margin-bottom: 14px;
        }

        ol {
            color: $color-text;
            padding-left: 18px;

            li {
                font-size: 14px;
                font-weight: 600;

                &:not(:last-child) {
                    margin-bottom: 18px;
                }

                &::marker {
                    margin-right: 4px;
                }
            }
        }
    }
}