.grade {
    font-family: $font-title;
    font-size: 15px;
    font-weight: 700;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: rotate(-55deg);
    width: 55px;
    height: 85px;
    margin-top: -15px;
    margin-bottom: -15px;

    &.color {
        &-green svg path {
            fill: $color-green
        }

        &-green-light svg path {
            fill: $color-green-light
        }

        &-yellow svg path {
            fill: $color-yellow
        }

        &-maroon svg path {
            fill: $color-maroon
        }

        &-blue svg path {
            fill: $color-blue
        }

        &-violet svg path {
            fill: $color-violet
        }
    }

    .text {
        @include absolcenter;
    }

    .shape {
        width: 100%;
        height: 100%;

        svg {
            width: 100%;
            height: 100%;
        }
    }
}