.navigation {
    background-color: rgba($color-black, .14);
    border-radius: 4px;
    padding: 0px 24px;
    min-height: 60px;
    display: flex;
    align-items: center;
}

.nav-list {
    width: 100%;
    list-style-type: none;
    padding-left: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-evenly;

    >li {

        >a {
            color: #fff;
            font-size: 12px;
            font-weight: 800;
            line-height: 60px;
            text-transform: uppercase;
        }

        &.has-submenu {
            position: relative;

            >a {
                position: relative;
                padding-right: 25px;

                &::after {
                    content: "";
                    position: absolute;
                    top: calc(50% + 3px);
                    transform: translateY(-50%);
                    right: 0;
                    border: 6px solid transparent;
                    border-top-color: #fff;
                }
            }

            ul {
                position: absolute;
                top: -1000%;
                left: 0px;
                transform: scaleY(0);
                background-color: #fff;
                box-shadow: 0 0 16px -12px rgba($color-black, .14);
                list-style-type: none;
                margin: 0;
                padding-left: 0;
                min-width: 160px;
                opacity: 0;
                transition: .35s opacity, .35s transform;
                transform-origin: 0 0;

                >li {
                    >a {
                        display: block;
                        font-size: 12px;
                        font-weight: 700;
                        padding: 10px 12px;
                    }
                }
            }

            &:hover {
                ul {
                    top: calc(100% - 1px);
                    transform: scaleY(1);
                    opacity: 1;
                }
            }
        }
    }
}