.align-items-center {
  align-items: center;
}

.justify-content-center {
  justify-content: center;
}

.trusted {
  display: inline-flex;
  align-items: center;

  .icon {
    display: inline-flex;
    width: 18px;
    margin-right: 9px;

    img {
      width: 100%;
    }
  }

  p {
    color: $color-primary-2;
    font-size: 12px;
    font-weight: 700;
    margin-bottom: 0;
  }
}