.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 14px;
    font-weight: 800;
    padding: 3px 33px;
    border: 0;
    border-radius: 48px;
    color: #fff;
    background-color: $color-primary-1;
    min-width: 137px;
    min-height: 39px;
    transition: all .3s ease-out;

    &:hover {
        color: $color-black;
        background-color: $color-gray;
    }

    &-purple {
        min-width: 195px;
        min-height: 49px;
        background-image: $gradient-bluepurple;
        background-size: 100% auto;
        background-position-x: 0%;

        &:hover {
            color: #fff;
        }
    }

    &-yellow {
        color: #fff;
        background-image: $gradient-yelloworange;
        background-size: 100% auto;
        background-position-x: 0%;

        &:hover {
            color: #fff;
        }
    }

    &-min-lg {
        font-size: 19px;
        min-width: 247px;
    }
}

.link-underline {
    color: $color-link;
    text-decoration: underline;
}