.homebanner {
    padding-top: 204px;

    &--section {
        padding: 56px 0 106px;

        .container {
            position: relative;
            border-radius: 14px;
            color: $color-gray;
            background: $gradient-bluepurple;

            &::before {
                content: "";
                z-index: -1;
                position: absolute;
                top: 60%;
                right: -7.5%;
                width: 134px;
                height: 134px;
                background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTEzIiBoZWlnaHQ9IjExMyIgdmlld0JveD0iMCAwIDExMyAxMTMiIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+CjxwYXRoIGQ9Ik04Mi4zOTg4IDAuMzQwODc2TDExMi40MzQgMTEyLjQzNEwwLjM0MDg4OSA4Mi4zOTg4TDgyLjM5ODggMC4zNDA4NzZaIiBmaWxsPSIjRUNFQ0VDIi8+Cjwvc3ZnPgo=");
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
            }
        }

        &.has-section-circlebg {
            &::after {
                z-index: -2;
                top: 40%;
            }
        }
    }

    h1 {
        color: #fff;
        font-family: $font-title;
        font-size: 64px;
        line-height: 68px;
        font-weight: 800;
        text-transform: capitalize;
        margin-bottom: 14px;
    }

    p {
        font-size: 18px;
        line-height: 28px;

        &:last-child {
            margin-bottom: 0;
        }

        small {
            color: #fff;
            font-size: 12px;
            opacity: .68;
        }
    }

    .row {}

    .col {
        &-text {
            display: flex;
            flex-flow: column;
            justify-content: flex-end;
            padding-bottom: 112px;
            padding-right: 50px;
        }

        &-media {
            display: flex;
            flex-flow: column;
            justify-content: flex-end;
            max-width: 537px;
            margin-right: -30px;

            img {
                width: 100%;
            }
        }
    }

    &-specs {
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        padding: 24px 52px;
        width: 100%;
        max-width: 940px;
        color: $color-black;
        background-color: #fff;
        border-radius: 108px;
        box-shadow: 0 0 40px rgba($color-primary, .19);

        .spec {
            display: inline-flex;
            align-items: center;

            .icon {
                max-width: 45px;
            }

            .text {
                padding-left: 20px;

                strong {
                    color: $color-primary;
                    font-size: 26px;
                    font-weight: 800;
                    margin-bottom: 4px;
                }

                p {
                    font-size: 13px;
                    font-weight: 500;
                    line-height: 14px;
                    margin-bottom: 0;
                }
            }
        }
    }
}

.banner-form {
    position: relative;
    display: flex;

    &-wrap {
        margin-bottom: 15px;
    }

    .formfield {
        &--select {
            width: 259px;
            margin-right: 7px;
        }

        &--email {
            flex: auto;
        }

        &--submit {
            position: absolute;
            top: 50%;
            right: 7px;
            transform: translateY(-50%);
        }
    }
}