.learncards {
    position: relative;

    &--section {
        padding-top: 86px;
        padding-bottom: 45px;
    }

    .section-head {
        max-width: 470px;
        @include margin-auto;
        margin-bottom: 63px;
    }

    .shape {

        &-1,
        &-2,
        &-3,
        &-4,
        &-5 {
            position: absolute;
            opacity: .5;

            img,
            svg {
                width: 100%;
                height: auto;
            }
        }

        &-1 {
            width: 38px;
            top: 4%;
            left: 16%;
        }

        &-2 {
            width: 14px;
            top: 2%;
            left: 26%;
        }

        &-3 {
            width: 56px;
            top: 6%;
            left: 29%;
        }

        &-4 {
            width: 35px;
            top: 2%;
            left: 77%;
        }

        &-5 {
            width: 108px;
            top: 3%;
            left: 82%;
        }
    }

    .section-body {
        .row {
            margin-left: -24px;
            margin-right: -24px;
        }

        [class^="col-"] {
            padding-left: 24px;
            padding-right: 24px;
            margin-bottom: 60px;
            display: flex;
            flex-flow: column;
        }
    }

    &-card {
        position: relative;
        padding: 47px 32px 54px;
        background-color: #fff;
        box-shadow: 0 0 24px rgba(#D5D7F9, .2);
        border-radius: 24px;
        flex: auto;

        .grade {
            position: absolute;
            top: -8px;
            left: 8px;
            margin: 0;

            &.color {
                &-green svg path {
                    fill: $color-green
                }

                &-green-light svg path {
                    fill: $color-green-light
                }

                &-yellow svg path {
                    fill: $color-yellow
                }

                &-maroon svg path {
                    fill: $color-maroon
                }

                &-blue svg path {
                    fill: $color-blue
                }

                &-violet svg path {
                    fill: $color-violet
                }
            }

            .text {
                @include absolcenter;
            }

            .shape {
                width: 100%;
                height: 100%;

                svg {
                    width: 100%;
                    height: 100%;
                }
            }
        }

        .media {
            z-index: 1;
            position: relative;
            width: 137px;
            @include margin-auto;
            margin-bottom: 24px;

            img {
                @include imgfit;
            }

            .bg {
                z-index: -1;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) rotate(8.25deg);
                width: 100%;
                opacity: .4;
            }
        }

        .btn {
            position: absolute;
            top: 100%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: calc(100% - 64px);
        }
    }
}