$color-primary: #5E79D6;

$color-primary-1: #5F9FFF;
$color-primary-2: #ABCDFF;


$color-purple: #9462FF;

$color-yellow: #F6B53F;
$color-yellow-light: #FFE2AD;

$color-orange: #FF9227;

$color-link: #32B5FF;

$color-green-light: #8CE093;
$color-green: #58C59D;
$color-green-1: #6DCC41;

$color-green-1-bg: rgba(#60C032, .14);

$color-maroon: #CE8391;

$color-violet: #C163C9;
$color-violet-text: #A497C1;
$color-violet-bg: #F6F1FF;

$color-blue: #50C6EC;
$color-blue-1: #446BAA;
$color-blue-2: #6F9FFD;
$color-blue-2-bg: #F0F9FF;

$color-black: #000000;
$color-black-1: #2C2C2C;
$color-black-2: #6D6D6D;
$color-black-3: #878787;

$color-gray: #F5F5F5;
$color-gray-1: #9E9E9E;
$color-gray-2: #F3F3F3;

$color-text: #6A6B6C;
$color-text-1: #9E9E9E;
$color-text-2: #3a3a3a;
$color-text-light: #B9B9B9;

$color-border: #E1E1E1;
$color-border-1: #979797;

$color-shadow: #D1DEFF;

$gradient-bluepurple: linear-gradient(150deg, $color-primary, $color-purple);
$gradient-yelloworange: linear-gradient(150deg, $color-yellow, $color-orange);

.color {
    &-primary {
        color: $color-primary;
    }

    &-primary-1 {
        color: $color-primary-1;
    }

    &-primary-2 {
        color: $color-primary-2;
    }

    &-purple {
        color: $color-purple;
    }

    &-yellow {
        color: $color-yellow;
    }

    &-orange {
        color: $color-orange;
    }

    &-black {
        color: $color-black;
    }

    &-black-1 {
        color: $color-black-1;
    }

    &-black-2 {
        color: $color-black-2;
    }

    &-black-3 {
        color: $color-black-3;
    }

    &-gray {
        color: $color-gray;
    }

    &-gray-1 {
        color: $color-gray-1;
    }

    &-gray {
        color: $color-gray;
    }
}

.bgcolor {
    &-primary {
        color: $color-primary;
    }

    &-primary-1 {
        color: $color-primary-1;
    }

    &-primary-2 {
        color: $color-primary-2;
    }

    &-purple {
        color: $color-purple;
    }

    &-yellow {
        color: $color-yellow;
    }

    &-orange {
        color: $color-orange;
    }

    &-black {
        color: $color-black;
    }

    &-black-1 {
        color: $color-black-1;
    }

    &-black-2 {
        color: $color-black-2;
    }

    &-black-3 {
        color: $color-black-3;
    }

    &-gray {
        color: $color-gray;
    }

    &-gray-1 {
        color: $color-gray-1;
    }

    &-gray {
        color: $color-gray;
    }
}