.header {
    z-index: 99;
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    background: $gradient-bluepurple;
    padding: 15px 0 32px;
    margin-bottom: 28px;

    &-top {
        padding: 16px 0 0;

        .col {
            &-right {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                text-align: right;

                .trusted {
                    margin-right: 20px;
                }
            }
        }

        .logo {
            width: 300px;

            img {
                @include imgfit;
            }
        }
    }

    &-bot {
        padding-top: 8px;
    }

    &-login {
        .btn {
            font-size: 12px;
        }
    }
}

.home {
    .header {
        position: absolute;
        top: 56px;
        padding-top: 15px;
        background: none;

        &-bot {
            padding-top: 11px;
        }
    }
}